























import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import dayjs from 'dayjs'
import clone from '@/lib/clone';

const now = new Date()
const oneDay = 86400*1000
@Component({
  components: {Tabs}
})
export default class Statistics extends Vue {
  name = 'statistics';
  type = '-';
  typeList = recordTypeList;

  beautify(string:string){
    if(dayjs(string).isSame(now,'day')){
      return '今天'
    }else if(dayjs(string).isSame(now.valueOf() - oneDay,'day')){
      return  '昨天'
    }else if(dayjs(string).isSame(now.valueOf() - 2*oneDay,'day')){
      return '前天'
    }else {
      return dayjs(string).format('YYYY年MM月DD日')
    }
  }
  beforeCreate() {
    this.$store.commit('fetchRecords');
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get groupList() {
    const recordList = this.recordList;
    if (recordList.length === 0) {return [];}
    type HashTableValue = {title:string,items:RecordItem[],total?:number}[];

   const newList = clone(recordList).filter(r=>r.type === this.type).sort((a,b)=>dayjs(b.createAt).valueOf() - dayjs(a.createAt).valueOf())
    if(newList.length === 0) {
      return []
    }
    const result:HashTableValue = [{title:dayjs(newList[0].createAt).format('YYYY-MM-DD'),items:[newList[0]]}]
    for (let i = 1;i<newList.length;i++){
      const current = newList[i]
      const last = result[result.length - 1]
      if(dayjs(last.title).isSame(dayjs(current.createAt),'day')){
          last.items.push(current)
      }else {
        result.push({title:dayjs(current.createAt).format('YYYY-MM-DD'),items:[current]})
      }
    }
    result.map(group=>{
      group.total = group.items.reduce((sum,item)=>{return sum+item.amount},0)
    })
    return result
  }
  tagString(tags:Tag[]){
    return tags.length === 0 ? '无' : tags.map(t=>t.name).join(',')
  }
}
